<template>
    <el-table
        :data="tableData"
        stripe
        :max-height="600"
        style="width: 100%">

        <el-table-column
            prop="release_date"
            label="发布时间"
            width="180">
        </el-table-column>
        <el-table-column
            prop="version"
            label="版本号"
            width="180">
        </el-table-column>
        <el-table-column
            v-if="versionsType==='app'"
            prop="app_object"
            label="文件">
        </el-table-column>
        <el-table-column
            v-if="versionsType==='public'"
            prop="public_object"
            label="文件">
        </el-table-column>
        <el-table-column
            prop="remark"
            label="说明">
            <template slot-scope="scope">
                {{ scope.row.remark || '--' }}
            </template>
        </el-table-column>
        <el-table-column
            prop="version"
            label="操作">
            <template slot-scope="scope">
                <el-tag v-if="(versionsType==='app' && current.app===scope.row.version) || (versionsType==='public' && current.public===scope.row.version)">当前版本</el-tag>
                <el-button v-else size="mini" class="el-icon-upload2" @click="update(scope.row.version)"></el-button>
            </template>
        </el-table-column>
    </el-table>
</template>

<script>
export default {
    name: "Versions",
    props: {
        versionsType: {
            type: String,
            default: 'app'
        },
        current: {
            type: Object,
            // eslint-disable-next-line vue/require-valid-default-prop
            default: {
                app: '0.0.0',
                public: '0.0.0'
            }
        }
    },
    data() {
        return {
            isLoading: false,
            tableData: []
        }
    },
    watch: {
        versionsType() {
            this.load()
        }
    },
    mounted() {
        this.load()
    },
    methods: {
        update(version) {
            this.$confirm(`确定将${this.versionsType === 'app' ? `应用内核` : `资源文件`}更新至${version}吗?`).then(() => {
                this.$loading.show('升级中')
                this.$request.post('/update', {type: this.versionsType, version: version}).then(result => {
                    if (result.errCode) {
                        this.$message.error(result.message);
                    } else {
                        this.$message.success(`${result.data}个节点更新完毕`)
                        this.$emit('updated', result.data)
                    }
                }).catch(error => {
                    this.$message.error(error);
                }).finally(() => {
                    this.$loading.close()
                });
            })
        },
        load() {
            this.tableData = []
            this.isLoading = true
            this.$request.get('/versions', {type: this.versionsType}).then(result => {
                if (result.errCode) {
                    this.$message.error(result.message);
                    this.isLoading = false;
                } else {
                    this.tableData = result.data
                }
            }).catch(error => {
                this.$message.error(error);
            });
        }
    }
}
</script>