const Ws = {
    socket: null,
    events: [],
    init() {
        this.events = []
        return this;
    },
    connect(host) {
        let _that = this
        this.socket = new WebSocket(host)
        this.socket.onerror = function (error) {
            _that.emit('error', error)
        };
        this.socket.onopen = function () {
            _that.emit('connected')
            _that.socket.onmessage = function (message) {
                _that.emit('message', message.data)
            };
            _that.socket.onclose = function () {
                _that.emit('close')
            };
        };
    },
    close() {
        if (!this.socket) {
            return
        }
        this.socket.close()
    },
    send(event, data) {
        this.socket.send(JSON.stringify({event: event, data: data || null}));
    },
    on(event, fn) {
        if (!this.events[event]) {
            this.events[event] = [];
        }
        this.events[event].push(fn);
        return this;
    },
    emit(...args) {
        let type = args[0];
        let params = [].slice.call(args, 1);
        let fn = this.events[type];
        if (fn === undefined) {
            return this
        }
        fn.forEach((item) => {
            item.apply(this, params);//执行函数
        })
        return this;
    }
}
export default Ws;