<template>
    <div>
        <el-descriptions class="margin-top" direction="vertical" title="守护进程" :column="15" border>
            <template slot="title">
                <h4>【应用信息】App Info</h4>
            </template>
            <template slot="extra">
                <el-button type="primary" v-if="connectStatus.status===0" @click="initWebSocket()" size="mini" :loading="!socektUrl || isLoading">
                    <i class="el-icon-link el-icon--left" v-if="socektUrl && !isLoading"></i>连接
                </el-button>
                <el-dropdown @command="handleCommand" trigger="click" v-if="connectStatus.status===1">
                    <span class="el-dropdown-link cursor-pointer"><el-tag :type="connectStatus.status === 1?'success':'danger'">{{ connectStatus.status === 1 ? '已连接' : '未连接' }}
                        <i v-if="connectStatus.status === 1" :class="{'el-icon-arrow-down':!isRestartting,'el-icon-loading':isRestartting}"></i></el-tag>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item command="refreshServer" divided :disabled="connectStatus.status === 0">
                            <i class="el-icon-refresh"></i>刷新
                        </el-dropdown-item>
                        <el-dropdown-item command="restartServer" :disabled="connectStatus.status===0">
                            <i :class="{'el-icon-refresh-right':running !== 1,'el-icon-video-play':running === 0}"></i>重启
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </template>
            <template v-if="connectStatus.status===1">
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-time"></i>
                        APPID
                    </template>
                    {{ info.appid || '--' }}
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-folder-opened"></i>
                        应用路径
                    </template>
                    <span>{{ info.app_path || '--' }}</span>
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-document"></i>
                        内核版本
                    </template>
                    <span @click="displayVersions('app')" class="cursor-pointer c-blue"> {{ latestVersion.app || '--' }}</span>
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-files"></i>
                        资源版本
                    </template>
                    <span @click="displayVersions('public')" class="cursor-pointer c-blue">  {{ latestVersion.public || '--' }}</span>
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-date"></i>
                        更新时间
                    </template>
                    <span>{{ info.updated || '--' }}</span>
                </el-descriptions-item>
<!--                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">-->
<!--                    <template slot="label">-->
<!--                        <i class="el-icon-bank-card"></i>-->
<!--                        主节点-->
<!--                    </template>-->
<!--                    {{ info.master || 0 }}-->
<!--                </el-descriptions-item>-->
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-share"></i>
                        子节点
                        <el-button size="mini" type="text" icon="el-icon-plus" @click="addNode"></el-button>
                    </template>
                    {{ info.slave || 0 }}

                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-document"></i>
                        运行日志
                        <i class="el-icon-more cursor-pointer fs-14 fr" @click="showLogs('info')"></i>
                    </template>
                    <span class="fs-36 cursor-pointer" @click="showLogs('info')">{{ logs.info.total }}</span>
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-warning-outline" :class="{'c-orange':logs.slow.total>0}"></i>
                        慢日志
                        <i class="el-icon-more cursor-pointer fs-14 fr" @click="showLogs('slow')"></i>
                    </template>
                    <span class="fs-36 cursor-pointer" @click="showLogs('slow')">{{ logs.slow.total }}</span>
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-warning-outline" :class="{'c-red':logs.error.total>0}"></i>
                        错误日志
                        <i class="el-icon-more cursor-pointer fs-14 fr" @click="showLogs('error')"></i>
                    </template>
                    <span class="fs-36 cursor-pointer" @click="showLogs('error')">{{ logs.error.total }}</span>
                </el-descriptions-item>

                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-monitor"></i>
                        控制台消息
                        <i class="el-icon-more cursor-pointer fs-14 fr" @click="consoleDisplay=true"></i>
                    </template>
                    <span class="fs-36 cursor-pointer" @click="consoleDisplay=true">{{ consoleLogs.length }}</span>
                </el-descriptions-item>

                <!--                <el-descriptions-item>-->
                <!--                    <template slot="label">-->
                <!--                        <i class="el-icon-monitor"></i>-->
                <!--                        控制台-->
                <!--                        <i class="el-icon-more cursor-pointer fs-14 fr" @click="consoleDisplay=true"></i>-->
                <!--                    </template>-->
                <!--                    <div class="block pd-5" style="position: relative;">-->
                <!--                        <transition-group name="log-fade" mode="out-in">-->
                <!--                            <p class="fs-12" v-for="item in  consoleLogs.slice(0,1)" :key="item.time">-->
                <!--                                【{{ item.time }}】{{ item.message.data }} </p>-->
                <!--                        </transition-group>-->
                <!--                    </div>-->
                <!--                </el-descriptions-item>-->
            </template>
        </el-descriptions>
        <el-divider></el-divider>
        <template v-if="connectStatus.status===1">
            <div v-for="(item,key) in servers" v-bind:key="key">
                <el-descriptions class="margin-top" :title=" `【${item.name}】${item.script}`" :column="9" border :labelStyle="{'max-width': '100px'}">
                    <template slot="title">
                        <h4>{{ `【${item.id}@${item.ip}:${item.port}】` }}</h4>
                    </template>
                    <template slot="extra">
                       <span>
                                <el-tag :type="isWaitting || isRestartting?'info':item.online?'success':'danger'">{{ isWaitting || isRestartting ? '等待中' : item.online ? '在线' : '离线' }}
                            </el-tag></span>
                    </template>
                    <template>
                        <el-descriptions-item>
                            <template slot="label">
                                <i class="el-icon-star-off"></i>
                                节点角色
                            </template>
                            {{ item.role }}
                        </el-descriptions-item>
                        <el-descriptions-item>
                            <template slot="label">
                                <i class="el-icon-connection"></i>
                                IP地址
                            </template>
                            {{ `${item.ip}:${item.port}` }}
                        </el-descriptions-item>
                        <el-descriptions-item>
                            <template slot="label">
                                <i class="el-icon-s-unfold"> 运行模式</i>
                            </template>
                            {{ item.server_run_mode }}
                        </el-descriptions-item>
                        <el-descriptions-item>
                            <template slot="label">
                                <i class="el-icon-receiving"> swoole版本</i>
                            </template>
                            {{ item.swoole_version }}
                        </el-descriptions-item>
                        <el-descriptions-item>
                            <template slot="label">
                                <i class="el-icon-files"></i>
                                内核版本
                            </template>
                            <el-tooltip :content="`updated at ${item.app_version} from ${item.app_version},comment:${item.app_version}`" placement="top">
                                <el-tag :type="item.app_version===latestVersion.app?'success':'warning'"> {{ item.app_version }}</el-tag>
                                >
                            </el-tooltip>
                        </el-descriptions-item>
                        <el-descriptions-item>
                            <template slot="label">
                                <i class="el-icon-document"></i>
                                资源版本
                            </template>
                            <el-tooltip :content="`updated at ${item.public_version} from ${item.public_version},comment:${item.public_version}`" placement="top">
                                <el-tag :type="item.public_version===latestVersion.public?'success':'warning'">{{ item.public_version }}</el-tag>
                            </el-tooltip>
                        </el-descriptions-item>
                        <el-descriptions-item>
                            <template slot="label">
                                <i class="el-icon-s-unfold"></i>
                                MID
                            </template>
                            {{ item.manager_pid }}
                        </el-descriptions-item>
                        <el-descriptions-item>
                            <template slot="label">
                                <i class="el-icon-aim"></i>
                                PID
                            </template>
                            {{ item.master_pid || '--' }}
                        </el-descriptions-item>
                        <el-descriptions-item>
                            <template slot="label">
                                <i class="el-icon-refresh"></i>
                                重启次数
                            </template>
                            <span>{{ item.restart_times }}</span>
                        </el-descriptions-item>
                    </template>
                </el-descriptions>
                <el-descriptions class="mt-20" title="" :column="20" direction="vertical" border :colon="false" v-if="item.server_stats!==null">
                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-time"></i>
                            运行时间
                        </template>
                        <span v-if="item.online">{{ Math.floor(new Date().getTime() - item.started * 1000) / 1000|secondToHour }}</span>
                        <span v-else>--</span>
                    </el-descriptions-item>
                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-s-custom"></i>
                            连接数量
                        </template>
                        <span class="fs-36">{{ item.server_stats.connection_num || 0 }}</span>
                    </el-descriptions-item>
                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-share"></i>
                            并发请求
                        </template>
                        <span class="fs-36">{{ item.http_request_count_current }}</span>
                    </el-descriptions-item>
                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-loading"></i>
                            等待响应
                        </template>
                        <span class="fs-36">{{ item.http_request_processing }}</span>
                    </el-descriptions-item>
                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-coin"></i>
                            Mysql并发
                        </template>
                        <span class="fs-36">{{ item.mysql_execute_count }}</span>
                    </el-descriptions-item>
                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-s-unfold"></i>
                            今日请求
                        </template>
                        <span class="fs-36">{{ item.http_request_count_today }}</span>
                    </el-descriptions-item>
                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-scissors"></i>
                            拒绝请求
                        </template>
                        <span class="fs-36">{{ item.http_request_reject }}</span>
                    </el-descriptions-item>
                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-cpu"></i>
                            Task数量
                        </template>
                        <span class="fs-36">{{ item.server_stats.tasking_num || 0 }}</span>
                    </el-descriptions-item>
                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-user"></i>
                            空闲Worker
                        </template>
                        <span class="fs-36">{{ item.server_stats.idle_worker_num || 0 }}</span>
                    </el-descriptions-item>

                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-cpu"></i>
                            CPU核数
                        </template>
                        <span class="fs-36">{{ item.cpu_num }}</span>
                    </el-descriptions-item>
                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-cpu"></i>
                            内存表
                        </template>
                        <span class="fs-36">{{ item.tables.length }}</span>
                    </el-descriptions-item>

                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-cpu"></i>
                            协程数量
                        </template>
                        <span class="fs-36">{{ item.threads }}</span>
                    </el-descriptions-item>
                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-cpu"></i>
                            事件数量
                        </template>
                        <span class="fs-36">{{ item.thread_status.event_num }}</span>
                    </el-descriptions-item>
                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-cpu"></i>
                            IO线程数
                        </template>
                        <span class="fs-36">{{ item.thread_status.aio_worker_num }}</span>
                    </el-descriptions-item>
                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-film"></i>
                            协程C栈
                        </template>
                        <span class="fs-36"> {{ (item.thread_status.c_stack_size / 1024 / 1024).toFixed(0) }}</span>M
                        <!--                                                <el-progress type="circle" :width="60" :percentage="Number((item.stack_useage /item.stack_useage*100).toFixed(2))" :color="customColors"></el-progress>-->
                    </el-descriptions-item>

                    <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                        <template slot="label">
                            <i class="el-icon-tickets"></i>
                            定时任务
                            <i class="el-icon-more cursor-pointer fs-14 fr" @click="showTasks()"></i>
                        </template>
                        <span class="fs-36 cursor-pointer" @click="showTasks()">{{ item.tasks.length }}</span>
                    </el-descriptions-item>
                </el-descriptions>
                <el-divider v-if="key!==servers.length-1"></el-divider>
            </div>
        </template>
        <el-drawer :title="logParams.type" :visible.sync="logDialogDisplay" :direction="direction" size="80%" :show-close="true">
            <div slot="title" style="padding-left: 10px">
                <span>[{{ logParams.type }}]累计{{ logParams.count }}条日志<i class="el-icon-refresh cursor-pointer" @click="getLogs()"></i></span>
                <el-date-picker
                    v-model="logParams.day"
                    type="date"
                    format="yyyy-MM-dd"
                    value-format="yyyy-MM-dd"
                    @change="getLogs()"
                    class="fr"
                    :editable="false"
                    :clearable="false"
                    placeholder="选择日期">
                </el-date-picker>
            </div>
            <div class="block pd-20">
                <span v-if="!logList.length">暂无日志</span>
                <el-timeline v-if="logList.length">
                    <el-timeline-item placement="top" v-for="(item,index) in logList" :timestamp="item.date" :key="index">
                        <el-card>
                            <h4>【{{ item.host || '--' }}】{{ item.module || 'Server' }}:{{ item.file || '--' }}</h4>
                            <p class="mt-10">
                                <json-viewer
                                    :value=" item.message"
                                    :expand-depth=10
                                    copyable
                                    v-if="logParams.type==='slow'"
                                    expanded></json-viewer>
                                <el-alert v-else :type="item.backtrace!==undefined?'error':'info'" :closable="false">{{ item.message }}</el-alert>
                            </p>
                            <template v-if="item.backtrace!==undefined && item.backtrace.length>0">
                                <p style="margin-top: 5px;word-break: break-all;" v-for="(backtrace,eKey) in item.backtrace" v-bind:key="eKey" v>{{ `${backtrace.file || backtrace.class}:${backtrace.line || backtrace.function}` }}</p>
                            </template>
                        </el-card>
                    </el-timeline-item>
                </el-timeline>
            </div>
            <!--翻页-->
            <el-pagination
                v-if="logList.length"
                background
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page.sync="logParams.page"
                :page-sizes="[10, 20, 50,100]"
                :page-size="logParams.size"
                layout="total,sizes, prev, pager, next"
                :total="logParams.count">
            </el-pagination>
        </el-drawer>
        <el-drawer title="控制台" :visible.sync="consoleDisplay" :direction="direction" size="80%" :show-close="true">
            <div class="block pd-20">
                <span v-if="!consoleLogs.length">暂无日志</span>
                <el-timeline v-if="consoleLogs.length">
                    <el-timeline-item placement="top" v-for="(item,index) in consoleLogs" :timestamp="item.time" :key="index">
                        <el-card>
                            <h4>{{ item.node }}</h4>
                            <p style="margin-top: 5px;word-break: break-all;">{{ item.message.data }}</p>
                        </el-card>
                    </el-timeline-item>
                </el-timeline>
            </div>
        </el-drawer>
        <!--版本管理-->
        <el-dialog title="版本管理" :visible.sync="dialogVersionsVisible" :close-on-click-modal="false" width="80%" v-if="servers.length">
            <versions :versionsType="versionsType" :current="{app:servers[0].app_version,public:servers[0].public_version}" @updated="updated"/>
        </el-dialog>
        <!--新增节点-->
        <el-dialog title="新增节点" :visible.sync="dialogAddnodeVisible" :close-on-click-modal="false" width="50%" v-if="servers.length">
            <el-form :model="input" status-icon ref="form" label-width="120px" class="">
                <el-form-item class="ml-5" label="主机地址：">
                    <el-input placeholder="请输入主机地址,确保master节点可访问,http://" v-model="input.host" type="text"></el-input>
                </el-form-item>
                <el-form-item class="ml-5" label="端口号：">
                    <el-input placeholder="请输入端口号,示范:9582" v-model="input.port" type="text"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogAddnodeVisible = false">取 消</el-button>
                <el-button type="primary" @click="installNode()" :loading="isLoading">{{ text }}</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import moment from "moment";
import Ws from '../../services/Ws'
import Versions from "@/views/server/components/Versions";
import JsonViewer
    from 'vue-json-viewer'

window.serverStatusTimer = null

export default {
    name: "Manage",
    components: {Versions, JsonViewer},
    data() {
        return {
            isLoading: false,
            text: '新增节点',
            input: {
                host: 'http://127.0.0.1',
                port: '19582'
            },
            dialogVersionsVisible: false,
            dialogAddnodeVisible: false,
            versionsType: 'app',
            customColors: [
                {
                    color: '#5cb87a',
                    percentage: 85
                },
                {
                    color: '#1989fa',
                    percentage: 88
                },
                {
                    color: '#e6a23c',
                    percentage: 90
                },
                {
                    color: '#f56c6c',
                    percentage: 100
                }
            ],
            isWaitting: false,
            isRestartting: false,
            socket: null,
            logDialogDisplay: false,
            consoleDisplay: false,
            taskDisplay: false,
            direction: 'rtl',
            socektUrl: '',
            running: 0,
            connectStatus: {
                status: 0
            },
            servers: [],
            logs: {
                error: {
                    count: 0,
                    list: []
                },
                info: {
                    count: 0,
                    list: []
                },
                slow: {
                    count: 0,
                    list: []
                }
            },
            info: {},
            latestVersion: {
                app: '',
                public: ''
            },
            logList: [],
            logParams: {
                type: 'error',
                page: 1,
                count: 1,
                size: 10,
                day: moment().format('YYYY-MM-DD'),
                task: '',
            },
            isPageActivation: false,
            serverStatusTimer: null,
            consoleLogs: [],
            bgTasks: [],
            selectedTaskIndex: '',
            selectedTask: {
                logs: []
            },
            selectedTaskLogs: []
        }
    },
    mounted() {
        this.$user.checkLogin(() => {
            this.getServers()
        })
    },
    methods: {
        addNode() {
            this.dialogAddnodeVisible = true
        },
        updated() {
            this.getServers()
            this.dialogVersionsVisible = false
        },
        installNode() {
            this.isLoading = true;
            this.$request.post('/install_slave_node', this.$copy(this.input)).then(result => {
                if (result.errCode) {
                    this.$message.error(result.message);
                    this.isLoading = false;
                } else {
                    this.text = '等待安装完成'
                    this.checkNode()
                }
            }).catch(error => {
                this.$message.error(error);
            })
        },
        checkNode() {
            this.$request.post('/check_slave_node', this.$copy(this.input)).then(result => {
                if (result.data === true) {
                    this.isLoading = false;
                    this.dialogAddnodeVisible = false
                    this.getServers()
                } else {
                    setTimeout(() => {
                        this.checkNode()
                    }, 5000)
                }
            }).catch(error => {
                this.$message.error(error);
                setTimeout(() => {
                    this.checkNode()
                }, 5000)
            })
        },
        displayVersions(type) {
            this.versionsType = type
            this.dialogVersionsVisible = true
        },
        displayTask() {
            this.selectedTask = this.bgTasks[this.selectedTaskIndex]
            this.selectedTaskLogs = this.$copy(this.selectedTask.logs).reverse()
        },
        initWebSocket() {
            //初始化weosocket
            this.socket = Ws.init()
            this.socket.on('connected', () => {
                this.connectStatus.status = 1
                setTimeout(() => {
                    this.socket.send('server_status')
                }, 100)
            }).on('message', data => {
                let result = JSON.parse(data);
                switch (result.event) {
                    case 'server_status':
                        this.servers = result.servers
                        this.logs = result.logs
                        this.info = result.info
                        break;
                    case 'console':
                        this.consoleLogs.unshift(result)
                        if (this.consoleLogs.length >= 300) {
                            this.consoleLogs.splice(-1)
                        }
                        break;
                }
            }).on('close', () => {
                this.connectStatus.status = 0
                this.isLoading = true
                setTimeout(() => {
                    this.reConnent(30)
                }, 2000)
            }).on('error', error => {

            }).connect(this.socektUrl)
        },
        reConnent(tryTimes) {
            if (this.connectStatus.status === 1 || tryTimes <= 0 || !this.isPageActivation) {
                this.isLoading = false
                this.isWaitting = false
            } else {
                tryTimes--
                this.initWebSocket()
                setTimeout(() => {
                    this.reConnent(tryTimes)
                }, 2000)
            }
        },
        showTasks() {
            this.$router.push('/server/crontab')
        },
        showLogs(type) {
            this.logParams.type = type
            this.logDialogDisplay = true
            this.logList = []
            this.getLogs()
        },
        getLogs() {
            this.isLoading = true;
            this.$request.get('/logs', this.logParams).then(result => {
                if (result.errCode) {
                    this.$message.error(result.message);
                } else {
                    this.logList = result.data.list;
                    this.logParams.count = result.data.total;
                }
            }).catch(error => {
                this.$message.error(error);
            }).finally(() => {
                this.isLoading = false;
            });
        },
        // 点击分页
        handleSizeChange(val) {
            this.logParams.size = val;
            this.getLogs();
        },
        handleCurrentChange(val) {
            this.logParams.page = val;
            this.getLogs();
        },
        getServers() {
            window.clearTimeout(window.serverStatusTimer)
            this.$api.system.servers().then(result => {
                if (result.errCode) {
                    return this.$message.error(result.message);
                }
                this.servers = result.data.servers
                this.logs = result.data.logs
                this.info = result.data.info
                this.latestVersion = result.data.latest_version
                this.isPageActivation = true
                this.socektUrl = result.data.socket_host
                if (!this.socket) {
                    this.initWebSocket()
                }
            }).catch(error => {
                this.$message.error(error)
            }).finally(() => {
            })
        },
        handleCommand(command) {
            if (command === 'refreshServer') {
                this.getServers()
            } else if (command === 'restartServer') {
                this.$confirm('确定重启所有节点吗')
                    .then(() => {
                        this.isWaitting = true
                        this.socket.send('restartAll')
                    })
                    .catch(() => {

                    });
            } else {
                let cmd = command.split(':')
                let action = cmd[0]
                let app = this.apps[cmd[1]].id
                this.isWaitting = true
                this.socket.emit("command", {
                    action: action,
                    app: app
                });
                if (action === 'showLog') {
                    //this.logDisplay = true
                } else {
                    this.apps[cmd[1]].status = 0
                }
            }
        }
    },
    beforeDestroy() {
        if (this.socket) {
            this.socket.close()
        }
        window.clearTimeout(window.serverStatusTimer)
        window.serverStatusTimer = null
        this.isPageActivation = false
    },
    destroyed() {
        if (this.socket) {
            this.socket.close()
        }
        window.clearTimeout(window.serverStatusTimer)
        window.serverStatusTimer = null
        this.isPageActivation = false
    }
}
</script>

<style scoped>
.log-fade-enter {
    opacity: 0;
    transform: translateY(150%);
    transform-origin: center center;
}

.log-fade-leave-to {
    opacity: 0;
    position: absolute;
    transform: translateY(-200%);
    transform-origin: center center;
}

.log-fade-enter-active {
    transition: all 300ms ease;
}

.log-fade-move {
    transition: all 500ms ease-out;
}

.log-fade-leave-active {
    transition: all 500ms ease;
    position: absolute;
}
</style>